* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

body {
  min-height: 100vh;
  transform-style: preserve-3d;
  background-color: #000;
  justify-content: center;
  align-items: center;
  display: flex;
}

.box {
  width: 200px;
  height: 200px;
  transform-style: preserve-3d;
  transition: all 1.5s;
  position: relative;
  transform: perspective(1000px)rotateY(0deg);
}

.box span {
  width: 100%;
  height: 100%;
  transform-origin: center;
  transform-style: preserve-3d;
  transform: rotateY(calc(var(--i) * 45deg) ) translateZ(300px);
  -webkit-box-reflect: below 0px linear-gradient(transparent, transparent, rgba(0, 0, 0, .267));
  position: absolute;
  top: 0;
  left: 0;
}

.box span img {
  width: 100%;
  height: 100%;
  object-fit: cover;
  -webkit-user-select: none;
  -ms-user-select: none;
  user-select: none;
  position: absolute;
  top: 0;
  left: 0;
}

.btns {
  color: beige;
  gap: 30px;
  display: flex;
  position: absolute;
  bottom: 100px;
}

.btns .btn {
  width: 60px;
  height: 60px;
  cursor: pointer;
  border: 2px solid #fff;
  border-radius: 50%;
  justify-content: center;
  align-items: center;
  font-size: 30px;
  display: flex;
  position: relative;
}

.btns .btn:hover {
  color: #000;
  background: #fff;
}

